.gameMenu {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameMenuOverlay {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    opacity: .5;
}

.gameMenuContent {
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    padding: 50px;
}
